<template>
  <div id="navi">
    <div class="navbar__logo">
      <h2>DM</h2>
    </div>
    <ul class="navbar__menu">
      <li
        class="navbar__menu__item active"
        @click="moveScroll()"
        data-link="
        #home"
      >
        Home
      </li>
      <li
        class="navbar__menu__item"
        @click="moveScroll()"
        data-link="
        #about"
      >
        About
      </li>
      <li
        class="navbar__menu__item"
        @click="moveScroll()"
        data-link="
        #skills"
      >
        Skills
      </li>
      <li
        class="navbar__menu__item"
        @click="moveScroll()"
        data-link="
        #work"
      >
        Work
      </li>
    </ul>
  </div>
  <home id="home" />
  <about id="about" />
  <skill id="skills" />
  <project id="work" />

  <div class="ocean">
    <div class="wave"></div>
    <div class="wave"></div>
  </div>
</template>

<script>
import home from "./components/PFHome.vue";
import about from "./components/PFAbout.vue";
import skill from "./components/PFSkill.vue";
import project from "./components/PFProject.vue";

export default {
  name: "App",
  components: {
    home,
    about,
    skill,
    project,
  },
  methods: {
    scrollIntoView(selector) {
      const scrollTo = document.querySelector(selector);
      scrollTo.scrollIntoView({ behavior: "smooth" });
    },
    navbarHeight() {
      const navbar = document.querySelector("#navi");
      if (window.scrollY > 100) {
        navbar.classList.add("navbar_dark");
      } else {
        navbar.classList.remove("navbar_dark");
      }
    },

    moveScroll() {
      const navbarMenu = document.querySelector(".navbar__menu");
      navbarMenu.addEventListener("click", (event) => {
        const target = event.target;
        const link = target.dataset.link;
        if (link == null) {
          return;
        }
        navbarMenu.classList.remove("open");
        this.scrollIntoView(link);
      });
    },
  },
  mounted() {
    document.addEventListener("scroll", this.navbarHeight);
  },
};
</script>

